
.sidebar {
    width: 170px;
    
    height: calc(100vh - 50px);
   
    position: fixed;
    top: 97px;
    
    left: 0;
    z-index: 1000;
    
    
    
    
    
  }
  
  .sidebar ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar li {
    padding: 10px;
  }
  
  
  
  
  .dropbtn {
    display: inline-block;
    color: rgb(0, 0, 0);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    
    font-size: 110%;
  }
  
  
  .dropdown:hover .dropbtn {
    background-color:#27012d;
    color: white;
    
  }
  
  li.dropdown {
    display: inline-block;
   
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #27012d;
    min-width: 160px;
    
    z-index: 1;
    margin-left: 60%;
   
    
  }
  
  .dropdown-content a {
    color: rgb(254, 251, 251);
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
   
  }
  
  .dropdown-content a:hover {
    background-color: #27012d;;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
    width: 15px;
  }
  
  .btn
  {
    background-color: #27012d;
    color: rgb(255, 255, 255);
  }
  .btn:hover
  {
    background-color: #27012d;
  }
  
  